'use strict';

try {
  window.addEventListener("message", function(event) {
      if (event.source != window) {
          return;
      }

      // This data can get stale, so if there's a problem on the web page we should
      // force a refresh of the cache to make sure
      if (event.data.type && (event.data.type === 'NUCLEUS_FORCE_REFRESH') && event.data.messageData) {
          chrome.runtime.sendMessage({ activity: 'nucleusMessage', body: event.data.messageData }, function(response) {
              if (response === true) {
                // Do nothing, this is just telling us the async call is ongoing.
              } else {
                // Pass it on to the postMessage so the web page can act.
                window.postMessage({ type:'NUCLEUS_REFRESH_COMPLETE', messageData:response  }, '*');
              } 
          });
      } else if (event.data.type && (event.data.type === 'NUCLEUS_FORCE_REFRESH') && !event.data.messageData) {
          // We didn't get any message data from website, bail out.
          window.postMessage({ type:'NUCLEUS_REFRESH_COMPLETE', messageData:undefined  }, '*');
      }

  }, true);

  if (window && window.sessionStorage) {
    window.sessionStorage.setItem('nucleus.installed', 'true');
  }
} catch (e) {
  // Swallow, so we don't affect the browser. 
}
